:root {
  font-size: 16px;
}

.container {
  text-align: center;
  box-sizing: border-box;
  width: 100%;
  max-width: 37.5rem;
  margin: 0 auto;
  padding: 3rem 1.25rem 0;
  position: relative;
}

.container-left {
  text-align: left;
  box-sizing: border-box;
  width: 100%;
  max-width: 37.5rem;
  margin: 0 auto;
  padding: 0 1.25rem;
  position: relative;
}

.container-left p {
  margin-bottom: 1rem;
}

.column {
  position: center;
  float: center;
  box-sizing: border-box;
  width: 100%;
}

@media (width >= 25rem) {
  .container {
    width: 85%;
    padding-left: 0;
    padding-right: 0;
  }
}

@media (width >= 34.375rem) {
  .container {
    width: 80%;
