  .column, .columns, .column:first-child, .columns:first-child {
    margin-left: 0;
  }
}

body {
  color: #1a1a1a;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Open Sans, system-ui;
  font-size: 1.125rem;
  font-weight: 400;
  line-height: 1.6;
}

h1 {
  letter-spacing: 0;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
  margin: 0 0 .5rem;
  font-size: 3rem;
  font-weight: 800;
  line-height: 1;
}

.container p {
  margin: 0 0 2rem;
}

:root {
  --scale-0: 1rem;
  --scale-1: 1.25rem;
  --scale-2: 1.563rem;
  --scale-3: 1.953rem;
  --scale-4: 2.441rem;
  --scale-5: 3.052rem;
  --spacing-xs: .5rem;
  --spacing-s: 1rem;
  --spacing-m: 1.5rem;
  --spacing-l: 2rem;
  --spacing-xl: 3rem;
  --spacing-xxl: 4rem;
  font-size: 16px;
}

.avatar {
  object-fit: cover;
  width: 8rem;
  height: 8rem;
  margin-bottom: var(--spacing-l);
  background-position: center;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

h1 {
  margin: 0 0 var(--spacing-m) 0;
  font-size: var(--scale-5);
  letter-spacing: -.02em;
  word-wrap: break-word;
  overflow-wrap: break-word;
  font-weight: 800;
  line-height: 1.1;
}

body {
  font-size: var(--scale-1);
  line-height: 1.6;
}

.container p {
  margin: 0 0 var(--spacing-xl) 0;
  font-size: var(--scale-1);
  line-height: 1.6;
}

.container {
  padding: var(--spacing-xl) var(--spacing-m) 0 var(--spacing-m);
  text-align: center;
}

footer {
  margin: var(--spacing-xxl) 0;
  font-size: var(--scale-0);
}

@media (width <= 34.375rem) {
  h1 {
    font-size: var(--scale-4);
  }

  body, .container p {
    font-size: var(--scale-0);
  }

  .avatar {
    margin-bottom: var(--spacing-m);
  }

  footer {
    margin: var(--spacing-xl) 0;
  }
}

a {
  color: #2457f5;
  text-decoration: underline;
}

a:hover {
  color: #083bda;
}

.button, button {
  white-space: normal;
  background-color: var(--button-background, transparent);
  width: 18.75rem;
  min-height: 3rem;
  color: var(--button-text, #000);
