  cursor: pointer;
  box-sizing: border-box;
  hyphens: auto;
  text-align: center;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  padding: .75rem 1rem;
  font-size: 1.125rem;
  font-weight: 700;
  line-height: 1.3;
  text-decoration: none;
  display: inline-flex;
}

.icon {
  flex-shrink: 0;
  width: 1.25rem;
  height: 1.25rem;
  margin-right: .5rem;
}

.avatar {
  object-fit: cover;
  width: 8rem;
  height: 8rem;
  margin-bottom: var(--spacing-l);
  background-position: center;
}

.avatar--none {
  border-radius: 0%;
}

.avatar--rounded {
  border-radius: 50%;
}

.avatar--soft {
  border-radius: .5rem;
}

:root.theme-dark {
  color-scheme: dark;
}

:root.theme-dark :focus-visible {
  outline-offset: 2px;
  outline: 2px solid #4899f7;
}

:root.theme-dark body {
  color: #fff;
  background-color: #121212;
}

:root.theme-dark a:not(.button) {
  color: #4899f7;
}

:root.theme-dark a:not(.button):hover {
  color: #7ab8ff;
}

:root.theme-auto {
  color-scheme: light dark;
}

@media (prefers-color-scheme: dark) {
  :root.theme-auto body {
    color: #fff;
    background-color: #121212;
  }

  :root.theme-auto :focus-visible {
    outline-offset: 2px;
    outline: 2px solid #4899f7;
  }

  :root.theme-auto a:not(.button) {
    color: #4899f7;
  }

  :root.theme-auto a:not(.button):hover {
    color: #7ab8ff;
  }
}

.button:hover, button:hover {
  color: var(--button-text);
}

@media (width <= 34.375rem) {
  h1 {
    font-size: 2rem;
  }

  body {
    font-size: 1rem;
  }
}

nav {
